
@font-face {
  font-family: 'Moldie';
  src: url('../assets/Moldie.otf') format('truetype');
}

.home .promo {
    margin-top: 50px;
}

.home .promo .largePromo {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 768px) {
  .home .promo .largePromo {
    width: 100%;
    height: auto;
    display: none;
  }

  .home .promo .smallPromo {
    width: 100%;
    height: auto;
    display: block;
  }
}

@media (min-width: 768px) {
    .home .promo .smallPromo {
        display: none;
    }
}

.home .body {
  display: flex;
  justify-content: center; 
  align-items: center;   
  height: 200px;  
}

.home .body .location {
  display: inline-block;
  border: 2px solid #000000;
  padding: 10px 20px;          
  border-radius: 5px;
  text-align: center;
  line-height: 1;
}

.home .body .location h1 {
  font-family: 'Moldie', sans-serif;
  line-height: 1;
  letter-spacing: 1.5px;
  color: #B80D29;
}

.home .body .location  p {
  font-family: 'Moldie', sans-serif;
  line-height: 1;
  color: #B80D29;
}

.footer {
  background-color: #B80D29;
  padding: 35px;
  text-align: center;
  color: white;
}